@font-face {
  font-family: 'Nanum Barun Gothic';
  font-style: normal;
  font-weight: 200;
  src: url('@{fontPath}/NanumBarunGothic-master/NanumBarunGothicUltraLight.eot');
  src: local('Nanum Barun Gothic UltraLight'),
       local('NanumBarunGothicUltraLight'),
       url('@{fontPath}/NanumBarunGothic-master/NanumBarunGothicUltraLight.eot?#iefix') format('embedded-opentype'),
       url('@{fontPath}/NanumBarunGothic-master/NanumBarunGothicUltraLight.woff2') format('woff2'),
       url('@{fontPath}/NanumBarunGothic-master/NanumBarunGothicUltraLight.woff') format('woff'),
       url('@{fontPath}/NanumBarunGothic-master/NanumBarunGothicUltraLight.ttf') format('truetype');
}

